/*Posts styles*/
.posts {
}
h4 {
    color: #09A76D;
    margin: 20px 10px 10px 10px;
}

textarea {
    border: 2px solid rgb(226, 226, 226);
    border-radius: 5px;
    margin: 5px;
}
textarea:hover {
    border: 2px solid #22B37F;
}

/*Post styles*/
.item {
    color: grey;
    display: grid;
    grid-template-areas: "ava message" "likes likes";
    grid-template-rows: 2fr 1fr;
    grid-template-columns: 60px 1fr;
    grid-gap: 10px;
}

.avatar {
    grid-area: ava;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    box-shadow: 0 0 0 1px #f5fff7, 0 0 2px #333;
    margin: 10px;
}

.message {
    grid-area: message;
}

.likes {
    grid-area: likes;
    color: #22B37F;
}
